body {
  margin: 24px;
}

.container {
  outline: 2px solid tomato;
  flex-wrap: wrap;
  display: flex;
}

button {
  margin: 4px;
}

.magic-btn {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 8px 16px;
  font-size: 16px;
  display: inline-flex;
}

.magic-btn > .icon {
  margin-left: 10px;
  font-size: 24px;
  display: inline-block;
}

.site-nav__link {
  color: currentColor;
  text-decoration: none;
}

.site-nav__link:hover {
  text-decoration: underline;
}

.site-nav__link--current {
  color: tomato;
}

.color-picker__option {
  width: 64px;
  height: 64px;
  cursor: pointer;
  border: none;
  outline: none;
  margin: 4px;
  display: inline-block;
}

#parent, #child, #descendant {
  color: #fff;
  text-transform: uppercase;
  border: 4px solid #212121;
  border-radius: 8px;
  margin: 16px;
  padding: 16px;
  font-size: 20px;
  font-weight: 500;
}

#parent {
  background-color: #673ab7;
}

#child {
  background-color: #448aff;
}

#descendant {
  background-color: #ff9800;
}

.tags {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: #fff;
  outline: 1px solid tomato;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 8px;
  list-style: none;
  display: flex;
}

.tags__item {
  outline: 1px solid #00f;
  margin: 4px;
  padding: 10px;
}

.tags__btn {
  color: #212121;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #212121;
  border-radius: 4px;
  outline: none;
  padding: 4px 10px;
  font-family: inherit;
  font-size: 24px;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
}

.tags__btn--active {
  color: #fff;
  background-color: #3f51b5;
}

